#header{
    display: flex;
    justify-content: flex-end;
}
#main{
    #title{
        font-size: 2rem;
        color: #00f3ff;
    }
    #dataSection{
        border: solid 2px #51d3c1;
        border-radius: 18px;
        padding: 1rem;
        margin-top: 1rem;
    }
    #statusData{
        padding: 1rem;
    }
    #actionSection{
        margin-top: 1rem;
        #depositSection{
            display: flex;
        }
        #withdrawSection{
            display: flex;
        }
        #approveAct{
            display: flex;
            justify-content: space-around;
        }
    }
    #userSection{
        margin-top: 1rem;
        #userList{
            margin-top: 1rem;
        }
    }
}
